import React, { PureComponent } from "react";
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";

export default class Image extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: props.data.postFileNew
    }
  }
  componentDidMount() {
    const { data } = this.props
    this.setState({ imgUrl: data.postFileNew })
  }
  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }
  componentWillReceiveProps(nextProps) {
    const { data } = nextProps;
    this.setState({ imgUrl: data.postFileNew })
  }
  render() {
    const { imgUrl } = this.state;
    const { data, wallID, ownerId } = this.props;
    const mediaType = (data.type === 3 || data.type === 5) ? true : false
    const isYoutubePost = (data.network.id === 7) ? true : false
    const multiImages = (data.imageList && data.imageList.length > 0) ? true : false
    const multiImagesClass = ((data.imageList && data.imageList.length) > 0 && (data.type === 3 || data.type === 5)) ? 'tb_spt_multi_images_ico_left' : 'tb_spt_multi_images_ico'
    const imageStyle = {
      backgroundImage: `url(${imgUrl})`, paddingBottom: '130%', width: '100%'
    };
    const isAudio = data.isAudio === 1 ? true : false

    return <div className="tb_spt_media_wrap">
      {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
      {mediaType ? isYoutubePost ? <div className="tb_spt_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
        isAudio ? <div className="tb_spt_audio_ico tb__icon tb-audio"><div></div></div> :
          <div className="tb_spt_video_ico tb__icon tb-video"><div></div></div> : ''}
      <div className="tb_spt_image" style={imageStyle}> </div>
      <img loading="lazy" src={imgUrl} alt={GET_MEDIA_NAME(imgUrl)} height={"400"} width={"400"} style={{ display: `none` }} data-link={data.link} data-load="0" data-network={data.network.id} data-wall-id={wallID} data-owner-id={ownerId} data-item-id={data.id} data-filter-id={data.filterId} onLoad={this.onLoad} onError={(data.stories == 0) ? (e) => {
        POPUP_IMAGE_RENEW_REQUEST(e)
      } : null} />
    </div>
  }
}
