import React, { PureComponent } from "react";
import Isotope, { data } from 'isotope-layout';
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../actions/themeActions';

class SquarePhoto extends PureComponent {
  state = {
    postData: [],
    loadData: 0
  }

  componentDidMount() {
    const { postData } = this.props;

    setTimeout(() =>
      this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
      , 50)

  }


  componentDidUpdate() {
    const self = this;
    window.addEventListener('resize', function (event) {
      self.editorHeight();
    }, true);
  }

  onLoadMasonry = () => {
    const { adjustWidth } = this.props;
    if (adjustWidth) {
      var elem = document.querySelector('.tb_sp_post_container');
      var iso = new Isotope(elem, {
        itemSelector: '.tb_sp_post_wrapper',
        layoutMode: 'masonry',
        masonry: {
          columnWidth: 1,
          horizontalOrder: false,
        }
      });
      setTimeout(() => iso.arrange(), 200)
    }

    this.editorHeight()
  }


  editorHeight = () => {
    var span = document.querySelector(".tb_sp_post_container").style.height;
    if (span && span.includes('px')) {
         const height = span.split("px");
         if(height  && JSON.stringify(height) && height[0]>0){
           this.props.managePostHeight(`${span}`);
       }
    }
  }

  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData }, () => this.onLoadMasonry())
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, cardNumber, onClickToCTA, postSizeInPx } = this.props;
    const { loadData } = this.state;
    let totalPostBestFit = postData.length;
    if (wall && wall.Personalization.fitRow == 1) {
      if (postData.length >= parseInt(cardNumber)) {
        let bestFitEachRow = postData.length / parseInt(cardNumber);
        totalPostBestFit = (parseInt(cardNumber) * parseInt(bestFitEachRow));
      }
    }

    return (
      <div className="tb_sp_post_container">
        {loadData ? ((postData && postData.length > 0)) && postData.map((item, index) => {
          const cardData = completeDataObject[item];
          return parseInt(totalPostBestFit) >= parseInt((index + 1)) ?
            <Card ownerId={wall.Wall.owner} itemData={cardData} key={index} itemIndex={index} adjustWidth={cardData.highlight == 1 ? adjustWidth * 2 : adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} />
            : null
        }) : null}
      </div>
    );
  }
}


const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}

const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SquarePhoto)