import React, { memo, useMemo } from "react";
import moment from 'moment';
const Time = (props) => {
  const { postTime, timeClass, authorColor } = props;
  const momentTime = useMemo(() => {
    return moment(new Date(postTime * 1000)).fromNow()
  }, [])

  // const momentTime = moment(new Date(postTime * 1000)).fromNow();
  const timeStyle = {
    color: authorColor
  };
  return <div className={timeClass} style={timeStyle}>{momentTime}</div>
}

export default memo(Time)

