import React, { PureComponent } from "react";
import Image from "../../Elements/Media/Image";

export default class Media extends PureComponent {
  render() {
    const { itemData, wallID, ownerId } = this.props;
    const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_cc_multi_images_ico_bottom' : 'tb_cc_multi_images_ico'
    const isAudio = itemData.isAudio === 1 ? true : false

    return <div className="tb_cc_media_wrap">
      {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}><div></div></div> : ''}
      {mediaType ? isYoutubePost ? <div className="tb_cc_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div>:
      isAudio ? <div className="tb_cc_audio_ico tb__icon tb-audio"><div></div></div> :
      <div className="tb_cc_video_ico tb__icon tb-video"><div></div></div> : ''}
      <Image ImageClass="tb_cc_image" ImageUrl={itemData.postFileNew} item={itemData} wallID={wallID} ownerId={ownerId} />
    </div>
  }
}


