import React, { PureComponent } from "react";
import ReactPlayer from 'react-player'
import Iframe from 'react-iframe';
import { POPUP_IMAGE_RENEW_REQUEST, POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import PopUpTikTok from "../../../Components/PostPopUp/Elements/PopUpTikTok";
const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "tumblr", "soundcloud", "linkedin"]

const VideoComponent = ({ data, WallId }) => {
  return <> {(data.link.indexOf("youtube") >= 0 || data.link.indexOf("youtu.be") >= 0) &&
    <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
      muted
      config={{
        file: {
          attributes: {
            autoPlay: true,
            muted: true
          }
        }
      }} />
  }
    {(data.link.indexOf("vimeo") >= 0) &&
      <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
        muted
        config={{
          file: {
            attributes: {
              autoPlay: true,
              muted: false
            }
          }
        }} />
    }
    {((data.network.id && data.network.id == 30) && data.link.indexOf("vk") >= 0) &&
      <Iframe
        className="tb-detail-image-iframe"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        src={`${data.mediaUrl}&autoplay=1`}
        allowFullScreen="true"
        style={{ width: "100%!important" }}
        data-filter-id={data.filterId}
        data-stories={data.stories}
        data-type="video"
        data-network={data.networkId}
        data-link={data.link}
        data-data-id={data.id}
        data-load={0}
      ></Iframe>

    }

    {(data.link.indexOf("soundcloud") >= 0) &&
      <Iframe
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        src={`${data.mediaUrl}`}
        allowFullScreen="true"
        style={{ width: "100%!important" }}
      ></Iframe>

    }

    {(data.link.indexOf("linkedin") >= 0 || data.link.indexOf("twitter") >= 0 || data.link.indexOf("instagram") >= 0 || data.link.indexOf("tumblr") >= 0 || data.link.indexOf("instagram") >= 0) && <video
      src={`${data.mediaUrl}`}
      autoPlay={true}
      muted={true}
      onError={(e) => { POPUP_IMAGE_RENEW_REQUEST(e) }}
      data-type="video"
      data-stories={data.stories}
      data-filter-id={data.filterId}
      data-network={data.networkId}
      data-postid={data.id}
      data-link={data.link}
      data-load={0}
      data-wall-id={WallId} data-item-id={data.id}
    ></video>
    }
  </>
}


export default class Video extends PureComponent {
  componentDidMount() {
    const { itemData } = this.props;
    let updateVideo = document.querySelector(`#video_p_${itemData.id}`);
    if (updateVideo) {
      updateVideo.play()
      updateVideo.muted = true;
    }
  }
  render() {
    const { itemData, wallID } = this.props;
    const isVideo = (videoLink) => {
      let isVideoStatus = false;
      if (String(videoLink)) {
        videoIframeTyle.map((item) => {
          if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
      }
      return isVideoStatus;

    }
    return (
      <div className="tb_stt_media_wrap">
        {
          isVideo(itemData.link) && (itemData.link.indexOf("youtube") >= 0) ? <Iframe
            className="tb_stt_media_iframe_video"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            src={`${itemData.mediaUrl}?autoplay=1&loop=1&mute=1&controls=0&volume=0`}
            allowFullScreen="true"
            data-filter-id={itemData.filterId} data-type="video"
            data-network={itemData.network.id}
            data-link={itemData.link}
            // data-wall-id={wall.Wall.id} data-item-id={itemData.id}
            data-load={0}
            height="100%"
            width="100%"
          ></Iframe> : (itemData.link.indexOf("twitter") >= 0) || (itemData.link.indexOf("facebook") >= 0) || (itemData.link.indexOf("linkedin") >= 0) || (itemData.link.indexOf("instagram") >= 0) || (itemData.network.id == 29) || (itemData.network.id == 21) ? <ReactPlayer
            className="tb_stt_media_iframe_video"
            url={itemData.mediaUrl} data-type="video"
            data-filter-id={itemData.filterId}
            data-network={itemData.network.id}
            data-link={itemData.link}
            // data-wall-id={wall.Wall.id} data-item-id={itemData.id}
            data-load={0}
            loop={true}
            playing
            playsinline={true}
            muted={true}
            volume={0}
            height="100%"
            width="100%"
          /> :
              (itemData.link.indexOf("vimeo") >= 0) ? <Iframe
                className="tb_stt_media_iframe_video"
                frameBorder="0"
                allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                src={`${itemData.mediaUrl}?autoplay=1&loop=1&muted=1&controls=0&volume=0`}
                allowFullScreen="true"
                data-filter-id={itemData.filterId} data-type="video"
                data-network={itemData.network.id}
                data-link={itemData.link}
                // data-wall-id={wall.Wall.id} data-item-id={itemData.id}
                data-load={0}
                height="100%"
                width="100%"
              ></Iframe> : (itemData.network.id == 30) && (itemData.link.indexOf("vk")) ? <Iframe
                className="tb_stt_media_iframe_video"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                src={`${itemData.mediaUrl}?&mute=1&autoplay=1&loop=1&controls=0&volume=0&playsinline=1`}
                allowFullScreen="true"
                style={{ width: "100%!important" }}
                data-filter-id={itemData.filterId}
                data-type="video"
                data-network={itemData.network.id}
                data-link={itemData.link}
                // data-wall-id={wall.Wall.id} data-item-id={itemData.id}
                data-load={0}
                height="100%"
              ></Iframe> :
                  itemData.network.id == 28 ?
                    <div className="tb_stt_media_iframe_video">
                      <PopUpTikTok data={itemData} key={itemData.id} />
                    </div>
                    : <video className="tb_stt_media_video"
                      id={`video_p_${itemData.id}`}
                      // data-wall-id={wall.Wall.id} data-item-id={itemData.id}
                      data-filter-id={itemData.filterId}
                      src={itemData.mediaUrl}
                      // muted={true}
                      // playsInline={true}
                      // autoPlay={true}
                      // loop={true}
                      // controls={false}
                      autoplay={true}
                      muted={true}
                      loop={true}
                      playsinline=""
                      data-type="video"
                      data-network={itemData.network.id}
                      data-link={itemData.link}
                      data-load={0}
                      onError={(itemData.stories == 0) ? (e) => { POPUP_VIDEO_RENEW(e) } : null}
                    ></video>

        }
        {/* <VideoComponent data={itemData} WallId={wallID} /> */}
      </div>
    );
  }
}
