import React, { PureComponent } from "react";
import Image from "./Image";
import Video from "./Video"
import ReactPlayer from 'react-player'
import Iframe from 'react-iframe';
import { POPUP_IMAGE_RENEW_REQUEST, POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME, loadImage } from '../../../../utils'
const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "tumblr", "soundcloud", "linkedin"]


const VideoComponent = ({ data, WallId }) => {
  return (data.type === 3 || data.type === 5) && <>
    {
      (data.link.indexOf("youtube") >= 0 || data.link.indexOf("youtu.be") >= 0) &&

      <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
        muted
        config={{
          file: {
            attributes: {
              autoPlay: true,
              muted: true
            }
          }
        }} />
    }
    {
      (data.link.indexOf("vimeo") >= 0) &&
      <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
        muted
        config={{
          file: {
            attributes: {
              autoPlay: true,
              muted: false
            }
          }
        }} />
    }
    {
      ((data.network.id && data.network.id == 30) && data.link.indexOf("vk") >= 0) &&
      <Iframe
        className="tb-detail-image-iframe"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        src={`${data.mediaUrl}&autoplay=1`}
        allowFullScreen="true"
        style={{ width: "100%!important" }}
        data-filter-id={data.filterId}
        data-stories={data.stories}
        data-type="video"
        data-network={data.networkId}
        data-link={data.link}
        data-data-id={data.id}
        data-load={0}
      ></Iframe>
    }
    {
      (data.link.indexOf("soundcloud") >= 0) &&
      <Iframe
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        src={`${data.mediaUrl}`}
        allowFullScreen="true"
        style={{ width: "100%!important" }}
      ></Iframe>

    }

    {
      (data.link.indexOf("linkedin") >= 0 || data.link.indexOf("twitter") >= 0 || data.link.indexOf("instagram") >= 0 || data.link.indexOf("tumblr") >= 0 || data.link.indexOf("instagram") >= 0) && <video
        src={`${data.mediaUrl}`}
        autoPlay={true}
        muted={true}
        onError={(e) => { POPUP_IMAGE_RENEW_REQUEST(e) }}
        data-type="video"
        data-stories={data.stories}
        data-filter-id={data.filterId}
        data-network={data.networkId}
        data-postid={data.id}
        data-link={data.link}
        data-load={0}
        data-wall-id={WallId} data-item-id={data.id}
      ></video>
    }
  </>

}
export default class Media extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: '100',
      width: '100',
      error: '',
      imgUrl: props.itemData.postFileNew
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { itemData } = props;
    const { imgUrl } = this.state;

    if (itemData && Object.keys(itemData).length > 0 && [4, 5, 3].includes(itemData.type)) {
      if (itemData.mediaHeight && itemData.mediaHeight != "" && itemData.mediaWidth && itemData.mediaWidth != "") {
        this.setState({
          height: (parseInt(itemData.mediaHeight) * 100) / parseInt(itemData.mediaWidth),
          width: (parseInt(itemData.mediaWidth) * 100) / parseInt(itemData.mediaHeight)
        })
      }
      else {

        const image = await loadImage(imgUrl);

        this.setState({
          height: (image.height * 100) / image.width,
          width: (image.width * 100) / image.height
        })
      }

    }
  }
  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }
  componentWillReceiveProps(nextProps) {
    const { itemData } = nextProps
    this.setState({ imgUrl: itemData.postFileNew }, () => this.onMediaLoad(nextProps))
  }
  render() {
    const { itemData, wallID, wall, ownerId } = this.props;
    const { height, imgUrl } = this.state
    const mediaType = (itemData.type === 3 || itemData.type === 5) && (itemData.network.id !== 28) && wall.ThemeRule.autoPlay == 1 ? true : false
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_stt_multi_images_ico_bottom' : 'tb_stt_multi_images_ico'
    const imageStyle = {
      backgroundImage: `url(${imgUrl})`, paddingBottom: '200%', width: '100%'
    };
    const isVideo = (videoLink) => {
      let isVideoStatus = false;
      if (String(videoLink)) {
        videoIframeTyle.map((item) => {
          if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
      }
      return isVideoStatus;

    }
    return (<>
      {/* {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''} */}
      {/* {mediaType ? isYoutubePost ? <div className="tb_stt_youtube_ico tb__icon tb-youtube"> </div> : <div className="tb_stt_video_ico tb__icon tb-play-outline"> </div> : ''} */}
      {mediaType ? <Video itemData={itemData} wallID={wallID} /> :
        <Image itemData={itemData} wallID={wallID} ownerId={ownerId} />}


      {/* <div className="tb_stt_image" style={imageStyle}> </div> */}
      {/* <VideoComponent data={itemData} WallId={wallID} /> */}
      <img loading="lazy" src={imgUrl} style={{ display: `none` }} alt={GET_MEDIA_NAME(imgUrl)} height={"400"} width={"400"} data-link={itemData.link} data-load="0" data-network={itemData.network.id} data-wall-id={wallID} data-item-id={itemData.id} data-filter-id={itemData.filterId} onLoad={this.onLoad} onError={(itemData.stories == 0) ? (e) => {
        POPUP_IMAGE_RENEW_REQUEST(e)
      } : null} />
    </>
    );
  }
}
