import React, { PureComponent } from "react";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";
export default class Media extends PureComponent {
  constructor(props) {
    super(props);
    this.mediaRef = React.createRef();
    this.state = {
      imgUrl: props.data.postFileNew,
      memdiaSize: {
        height: 400,
        width: 400
      }
    }
  }
  onLoad = event => {
    if (this.mediaRef.current) {
      //if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
      this.setState({
        memdiaSize: {
          height: this.mediaRef.current.offsetHeight,
          width: this.mediaRef.current.offsetWidth
        }
      })
    }
  }

  componentDidMount() {
    window.addEventListener('resize', this.onLoad);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onLoad);
  }

  render() {
    const { data, wallID, ownerId } = this.props;
    const { imgUrl, memdiaSize } = this.state;
    const mediaType = (data.type === 3 || data.type === 5) ? true : false
    const isYoutubePost = (data.network.id === 7) ? true : false
    const multiImages = (data.imageList && data.imageList.length > 0) ? true : false
    const multiImagesClass = (multiImages && mediaType) ? 'tb_hc_multi_images_ico_bottom' : 'tb_hc_multi_images_ico'
    const imageStyle = {
      paddingBottom: '100%', width: '100%'
    };
    const isAudio = data.isAudio === 1 ? true : false
    return <div className="tb_hc_media_wrap" role="img" aria-label="Post Image">
      {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : null}
      {mediaType ? isYoutubePost ? <div className="tb_hc_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
        isAudio ? <div className="tb_hc_audio_ico tb__icon tb-audio"><div></div></div> :
          <div className="tb_hc_video_ico tb__icon tb-video"><div></div></div> : ''}

        <div className="tb_hc_image_wrapper" style={imageStyle}>
          <LazyLoadImage wrapperClassName="tb_hc_image" effect="blur" delayMethod="debounce" delayTime="500" threshold="20"
            src={imgUrl}
            alt={GET_MEDIA_NAME(imgUrl)} height={memdiaSize.height} width={memdiaSize.width} data-link={data.link} data-load="0" data-network={data.network.id} data-wall-id={wallID} data-owner-id={ownerId} data-item-id={data.id} data-filter-id={data.filterId} onLoad={this.onLoad} onError={(data.stories == 0) ? (e) => {
              POPUP_IMAGE_RENEW_REQUEST(e)
            } : null}
            ref={this.mediaRef} 
            ratio={1.5} />
        </div>
    </div>
  }
}
