import React, { PureComponent, Component } from "react";
import imagesLoaded from 'imagesloaded'
import Isotope from 'isotope-layout';
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../actions/themeActions';

class ClassicCard extends PureComponent {
  state = {
    postData: [],
    loadData: 0
  }

  componentDidMount() {
    const { postData } = this.props;
    setTimeout(() =>
      this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
      , 100)
  }


  componentDidUpdate() {
    const self = this;
    window.addEventListener('resize', function (event) {
      self.editorHeight();
    }, true);
  }


  onLoadMasonry = () => {
    const { adjustWidth } = this.props;
    if (adjustWidth) {
      var elem = document.querySelector('.tb_cc_post_container');
      var iso = new Isotope(elem, {
        itemSelector: '.tb_cc_post_wrapper',
        layoutMode: 'masonry',
        // transitionDuration: `1s`,
        // stagger: 15,
        masonry: {
          columnWidth: 1,
          horizontalOrder: false,
        }

      });
      imagesLoaded(elem, (instance) => {
        iso.layout();
      });
    }

    this.editorHeight();
  }


  editorHeight = () => {
    var span = document.querySelector(".tb_cc_post_container");

        if (span) { 
          const getHeight = span.style.height;
          if(getHeight){
            const height = getHeight.split("px");
            if(height  && JSON.stringify(height) && height[0]>0){
              this.props.managePostHeight(`${getHeight}`);
          }
        }
    }
  }

  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall, languageSetting, clickToShowPopUp, onClickToCTA } = this.props;
    const { loadData } = this.state;

    return (
      <div className="tb_cc_post_container">
        {
          loadData ?
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return <Card ownerId={wall.Wall.owner} itemData={cardData} key={index} itemIndex={index} adjustWidth={cardData.highlight == 1 ? adjustWidth * 2 : adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} completeDataObject={completeDataObject} postData={postData} languageSetting={languageSetting} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} />
            }) : null
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClassicCard)
