import React, { PureComponent } from "react";
import { POPUP_IMAGE_RENEW_REQUEST, POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";
const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "tumblr", "soundcloud", "linkedin"]
export default class Image extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: props.data.postFileNew
    }
  }
  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }
  render() {
    const { imgUrl } = this.state;
    const { data, wallID, wall, ownerId } = this.props;
    const mediaType = (data.type === 3 || data.type === 5) ? true : false
    const isYoutubePost = (data.network.id === 7) ? true : false
    const multiImages = (data.imageList && data.imageList.length > 0) ? true : false
    const multiImagesClass = ((data.imageList && data.imageList.length) > 0 && (data.type === 3 || data.type === 5)) ? 'tb_hs_multi_images_ico_bottom' : 'tb_hs_multi_images_ico'
    const imageStyle = {
      backgroundImage: `url(${imgUrl})`, paddingBottom: '100%', width: '100%'
    };
    const isAudio = data.isAudio === 1 ? true : false
    const isVideo = (videoLink) => {
      let isVideoStatus = false;
      if (String(videoLink)) {
        videoIframeTyle.map((item) => {
          if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
      }
      return isVideoStatus;

    }
    // console.log("data.link, data.link.indexOf(facebook) >= 0", data.link, data.link.indexOf("youtube"), data.link.indexOf("vimeo") >= 0);
    return (
      <div className="tb_hs_media_wrap">
        {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
        {mediaType ? isYoutubePost ? <div className="tb_hs_youtube_ico tb__icon tb-youtube"><div></div></div> :
          isAudio ? <div className="tb_hs_audio_ico tb__icon tb-audio"><div></div></div> :
            <div className="tb_hs_video_ico tb__icon tb-video"><div></div></div> : ''}
        <div className="tb_hs_image" style={imageStyle}> </div>
        <img loading="lazy" src={imgUrl} style={{ display: `none` }} alt={GET_MEDIA_NAME(imgUrl)} height={"400"} width={"400"} data-link={data.link} data-load="0" data-network={data.network.id} data-wall-id={wallID} data-owner-id={ownerId} data-item-id={data.id} data-filter-id={data.filterId} onLoad={this.onLoad} onError={(data.stories == 0) ? (e) => {
          POPUP_IMAGE_RENEW_REQUEST(e)
        } : null} />
      </div>
    );
  }
}



